<template>
  <div>
      <div class="title">
        添加项目
      </div>
      <div class="MFprojectManagementBox">
        <el-steps :active="active">
            <el-step title="项目须知" description="项目新建所需要了解的内容"></el-step>
            <el-step title="填写项目信息" description="项目名称，内容等"></el-step>
            <el-step title="新建完毕" description="引导提示"></el-step>
        </el-steps>
        <!-- 步骤切换内容start -->
        <!-- 项目须知 1 -->
        <div v-show="active==0" class="TipBox">
            <div class="TipList">
                <svg class="icon icDefault">
                    <use xlink:href="#icon-wenjian"></use>  
                </svg>
                <div class="Tip">
                    创建项目：填写项目信息，向项目绑定网关
                </div>
            </div>
            <div class="TipList">
                <svg class="icon icDefault">
                    <use xlink:href="#icon-ziyuanxhdpi"></use>  
                </svg>
                <div class="Tip">
                    用户和权限：设置项目的成员，以及访问权限
                </div>
            </div>
            <div class="TipList">
                <svg class="icon icDefault">
                    <use xlink:href="#icon-duihaocheckmark17"></use>  
                </svg>
                <div class="Tip">
                    基本的项目创建与配置完成后，可以设置组态画面，进行图形化数据展示
                </div>
            </div>
            <div class="BtnBox">
                <el-button type="primary" @click="active=1">下一步</el-button>
            </div>
        </div>
        <!-- 填写项目信息2 -->
        <div class="TipBox_two" v-show="active==1">
            <!-- 提交信息的from表单 -->
            <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
                <el-form-item label="项目名称" prop="p_name">
                    <el-input v-model="ruleForm.p_name"></el-input>
                </el-form-item>
                <el-form-item label="画面地址" prop="p_dashboard">
                    <el-input v-model="ruleForm.p_dashboard"></el-input>
                </el-form-item>
                <el-form-item label="省" prop="p_province">
                    <el-input v-model="ruleForm.p_province"></el-input>
                </el-form-item>
                <el-form-item label="市" prop="p_city">
                    <el-input v-model="ruleForm.p_city"></el-input>
                </el-form-item>
                <el-form-item label="甲方单位" prop="p_party_a">
                    <el-input v-model="ruleForm.p_party_a"></el-input>
                </el-form-item>
                <el-form-item label="采购日期" prop="p_purchase_time">
                    <el-date-picker
                    v-model="ruleForm.p_purchase_time"
                    type="date"
                    format="yyyy 年 MM 月 dd 日"
                    value-format="yyyy-MM-dd"
                    placeholder="选择日期">
                    </el-date-picker>
                </el-form-item>
                <el-form-item label="竣工日期" prop="p_built_time">
                    <el-date-picker
                    v-model="ruleForm.p_built_time"
                    type="date"
                    format="yyyy 年 MM 月 dd 日"
                    value-format="yyyy-MM-dd"
                    placeholder="选择日期">
                    </el-date-picker>
                </el-form-item>
                <el-form-item label="联系人姓名" prop="p_contact_name">
                    <el-input v-model="ruleForm.p_contact_name"></el-input>
                </el-form-item>
                <el-form-item label="联系人的电话" prop="p_contact_number">
                    <el-input v-model="ruleForm.p_contact_number"></el-input>
                </el-form-item>
                <el-form-item label="客户类型" prop="p_customer_type">
                    <el-input v-model="ruleForm.p_customer_type"></el-input>
                </el-form-item>
                <el-form-item label="项目类型" prop="p_type">
                    <el-input v-model="ruleForm.p_type"></el-input>
                </el-form-item>
                <el-form-item label="地址" prop="p_address">
                    <el-input v-model="ruleForm.p_address"></el-input>
                </el-form-item>
                <el-form-item label="安装时间" prop="p_setup_time">
                    <el-date-picker
                    v-model="ruleForm.p_setup_time"
                    type="date"
                    format="yyyy 年 MM 月 dd 日"
                    value-format="yyyy-MM-dd"
                    placeholder="选择日期">
                    </el-date-picker>
                </el-form-item>
                <el-form-item label="项目简介" prop="p_description">
                    <el-input type="textarea" v-model="ruleForm.p_description"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="submitForm('ruleForm')">保存</el-button>
                    <el-button @click="resetForm('ruleForm')">重置</el-button>
                    <el-button @click="active=0">上一步</el-button>
                </el-form-item>
            </el-form>
        </div>
        <!-- 新建完毕3 -->
        <div class="TipBox_two" v-show="active==2 && Three==0">
            <div class="TipBox_two_tit">
                项目基础信息创建完成！
                <span>请根据需求进行如下操作</span>
            </div>
            <!-- 几个选项 -->
            <div class="ThreeTip">
                1、设置该项目对应的网关，显示采集的实时数据、历史数据和告警。
                <el-button class="ThreeTip_Btn" type="primary" @click="Three=1">添加采集设备</el-button>
            </div>
            <div class="ThreeTip">
                2、设置该项目对应的成员和访问权限。
                <el-button class="ThreeTip_Btn" type="primary" @click="Three=2">关联成员</el-button>
            </div>
            <div class="ThreeTip">
                3、设置该项目的组态画面。
                <el-button class="ThreeTip_Btn" type="primary" @click="Three=3">添加机械设备</el-button>
            </div>
            <!-- <div class="ThreeTip">
                4、设置该项目显示的功能模块。
                <el-button class="ThreeTip_Btn" type="primary" @click="Three=4">模块设置</el-button>
            </div> -->
            <el-button type="primary" class="complete" @click="Fncomplete">完成</el-button>
        </div>
        <!-- 步骤切换内容end -->
        <!-- 第三步中的按钮点击开始页面start -->
            <div v-show="Three==1">
                <el-button type="primary" style="margin-top:20px;margin-left:20px" @click="Three=0">返回</el-button>
                关联网关
            </div>
            <div v-show="Three==2">
                <el-button type="primary" style="margin-top:20px;margin-left:20px" @click="Three=0">返回</el-button>
                <router-view/>
            </div>
            <div v-show="Three==3">
                <el-button type="primary" style="margin-top:20px;margin-left:20px" @click="Three=0">返回</el-button>
                组态画面
            </div>
            <div v-show="Three==4">
                <el-button type="primary" style="margin-top:20px;margin-left:20px" @click="Three=0">返回</el-button>
                模块设置
            </div>
        <!-- 第三步中的按钮点击开始页面end -->
      </div>
  </div>
</template>

<script>
import url from "../../common/api";
import { mapState } from "vuex";
  export default {
    data() {
      return {
        active:0,//步骤条选中的状态
        Three:0,//步骤条选中的状态
        ruleForm: {//第二部的表单提交信息
            "p_name": "",
            "p_dashboard": "",
            "p_description": "",
            "p_type": "",
            "p_customer_type": "",
            "p_party_a": "",
            "p_purchase_time": "",
            "p_setup_time": "",
            "p_built_time": "",
            "p_contact_name": "",
            "p_contact_number": "",
            "p_province": "",
            "p_city": "",
            "p_address": "",
        },
        rules: {//表单信息验证
          p_name: [
            { required: true, message: '请输入项目名称', trigger: 'blur' }
          ],
          p_dashboard: [
            { required: true, message: '请输入组态画面', trigger: 'blur' }
          ],
          p_type: [
            { required: true, message: '请输入项目类型', trigger: 'blur' }
          ],
          p_party_a: [
            { required: true, message: '请输入甲方单位', trigger: 'blur' }
          ],
          p_province: [
            { required: true, message: '请输入省份', trigger: 'blur' }
          ],
          p_city: [
            { required: true, message: '请输入城市', trigger: 'blur' }
          ],
          p_address: [
            { required: true, message: '请输入地址', trigger: 'blur' }
          ],
        },
        project_id:-1,//当前创建项目成功后返回的id
      };
    },
    methods: {
      submitForm(formName) {//第二部的提交按钮
                  this.active=2//成功，近入下一个步骤
        this.$refs[formName].validate((valid) => {
          if (valid) {
            //   登陆内容中获取的公司——用户
            this.ruleForm.company_id=this.userInfo.u_company_id
            this.ruleForm.user_id=this.userInfo.user_id
            url.createproject(this.ruleForm)
            .then(res=>{
                console.log(res,'--------------创建提交的结果')
              if(res.data.code[0]=='200'){
                  this.active=2//成功，近入下一个步骤
                this.project_id=res.data.project_id//当前创建项目成功后返回的id
                this.$message.success(res.data.message[0]);
              }else{
                this.$message.error('当前项目创建失败，请重新创建');
              }
            })
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      resetForm(formName) {//第二部的重置按钮
        this.$refs[formName].resetFields();
      },
      Fncomplete(){//第三部 点击完成按钮出发的事件
        this.$router.push('/index/MFprojectManagement')
          console.log('第三部 点击完成按钮出发的事件')
      }
    },
    computed:{
        ...mapState(['userInfo'])
    }
  }
</script>

<style lang="stylus" scoped>
.title
    font-size 20px
    color #333
    margin-bottom 20px
    margin-top 20px
    padding-left 15px
    border-left 3px solid #409EFF
.MFprojectManagementBox
    padding 30px
    min-width 950px

.icDefault
    width 40px
    height 40px
    border-radius 50%
    background	#B0C4DE
    padding 20px
    color #3F3B3A
.BtnBox
    margin 30px 0 0 90px
.TipBox
    position relative
    margin 50px
.TipBox_two
    position relative
    margin 50px
    .TipBox_two_tit
        font-size 26px
        span 
            font-size 16px
            color #999
            display block
            margin-top 15px
            margin-left 10px
.TipBox:before
    content: '';
    position: absolute;
    display: block;
    width: 4px;
    height 302px
    background: #B0C4DE;
    top: 0;
    bottom: 0;
    margin-left: 38px;
    z-index -1
.TipList
    display flex
    margin 20px 0
    .Tip
        height 120px
        line-height 120px
        background #f1f1f1
        color #666
        padding-left 20px
        margin-left 30px
        position relative
        flex 1
.Tip::before
    content:"";
    position:absolute;
    top:28px;
    left:-20px;
    border-right:10px solid #f1f1f1;
    border-bottom:10px solid transparent;
    border-left:10px solid transparent;
    border-top:10px solid transparent;
.ThreeTip
    height 100px
    line-height 100px
    background #f1f1f1
    font-size 18px
    color #666
    padding-left 20px
    margin 25px 0
    position relative
    flex 1
    .ThreeTip_Btn
        float right
        margin-right 20px
        margin-top 30px
.complete
    width 160px
    height 45px
    display block
    font-size 18px
    margin 0 auto
</style>


